/* NOC Creation Panel Styles */
.noc-creation-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 1rem;
  padding: 1rem;
  overflow-y: auto;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.05) 100%);
}

/* Header */
.noc-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  gap: 1rem;
}

.noc-header h2 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
}

.noc-header p {
  margin: 0.25rem 0 0 0;
  color: var(--text-secondary);
}

.noc-actions {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

/* Main Content Layout */
.noc-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 1rem;
  flex: 1;
  overflow: hidden;
}

/* Left Panel - Form */
.noc-form-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  overflow-y: auto;
  padding-right: 0.5rem;
}

/* Form Group */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  display: block;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-primary);
}

.form-group input,
.form-group textarea,
.form-group select {
  padding: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
  font-family: inherit;
  font-size: 0.875rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: rgba(59, 130, 246, 0.5);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 10px rgba(59, 130, 246, 0.15);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: var(--text-tertiary);
}

.form-group small {
  font-size: 0.7rem;
  color: var(--text-tertiary);
  margin-top: -0.25rem;
}

/* Form Card */
.form-card {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 0.75rem;
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-card-title {
  margin: 0;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--text-primary);
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

/* License Info Section */
.license-info {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.license-field {
  flex: 1;
  min-width: 180px;
}

.license-field label {
  display: block;
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.license-field input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.375rem;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
  font-size: 0.875rem;
  font-weight: 600;
  font-family: 'Monaco', 'Courier New', monospace;
}

.license-field input:focus {
  border-color: rgba(59, 130, 246, 0.5);
  background: rgba(255, 255, 255, 0.08);
}

/* Conditions Section */
.conditions-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.condition-item {
  display: flex;
  gap: 0.75rem;
  align-items: flex-start;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
}

.condition-number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.75rem;
  height: 1.75rem;
  min-width: 1.75rem;
  border-radius: 0.375rem;
  background: rgba(59, 130, 246, 0.2);
  color: rgb(59, 130, 246);
  font-size: 0.75rem;
  font-weight: 700;
}

.condition-input {
  flex: 1;
  padding: 0.5rem;
  border: none;
  background: transparent;
  color: var(--text-primary);
  font-size: 0.875rem;
  outline: none;
}

.condition-input::placeholder {
  color: var(--text-tertiary);
}

.condition-remove {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.75rem;
  height: 1.75rem;
  border: none;
  background: rgba(239, 68, 68, 0.2);
  color: rgb(239, 68, 68);
  border-radius: 0.375rem;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 1.75rem;
}

.condition-remove:hover {
  background: rgba(239, 68, 68, 0.3);
}

.add-condition-btn {
  align-self: flex-start;
  padding: 0.5rem 1rem;
  border: 1px dashed rgba(59, 130, 246, 0.3);
  background: rgba(59, 130, 246, 0.1);
  color: rgb(59, 130, 246);
  border-radius: 0.375rem;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.add-condition-btn:hover {
  background: rgba(59, 130, 246, 0.15);
  border-color: rgba(59, 130, 246, 0.5);
}

/* Signatories Section */
.signatories-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.signatory-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.75rem;
  padding: 1rem;
}

.signatory-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.signatory-header h4 {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-primary);
}

.signatory-remove {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  border: none;
  background: rgba(239, 68, 68, 0.2);
  color: rgb(239, 68, 68);
  border-radius: 0.375rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.signatory-remove:hover {
  background: rgba(239, 68, 68, 0.3);
}

.signatory-fields {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
}

.signatory-field {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.signatory-field label {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.signatory-field input {
  padding: 0.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.375rem;
  background: rgba(255, 255, 255, 0.04);
  color: var(--text-primary);
  font-size: 0.8rem;
}

.signatory-field input:focus {
  outline: none;
  border-color: rgba(59, 130, 246, 0.3);
  background: rgba(255, 255, 255, 0.07);
}

.add-signatory-btn {
  padding: 0.75rem;
  border: 1px dashed rgba(16, 185, 129, 0.3);
  background: rgba(16, 185, 129, 0.1);
  color: rgb(16, 185, 129);
  border-radius: 0.5rem;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.add-signatory-btn:hover {
  background: rgba(16, 185, 129, 0.15);
  border-color: rgba(16, 185, 129, 0.5);
}

/* Right Panel - Preview */
.noc-preview-section {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  overflow-y: auto;
  padding-right: 0.5rem;
}

/* Preview Card */
.preview-card {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.75rem;
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.preview-title {
  margin: 0;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--text-primary);
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.preview-content {
  font-size: 0.8rem;
  line-height: 1.6;
  color: var(--text-secondary);
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Monaco', 'Courier New', monospace;
  max-height: 300px;
  overflow-y: auto;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.04);
  border-radius: 0.5rem;
}

.preview-content strong {
  color: var(--text-primary);
  font-weight: 700;
}

/* Format Reference */
.format-reference {
  position: relative;
}

.format-toggle {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid rgba(59, 130, 246, 0.3);
  background: rgba(59, 130, 246, 0.1);
  color: var(--text-primary);
  border-radius: 0.5rem;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.format-toggle:hover {
  background: rgba(59, 130, 246, 0.15);
  border-color: rgba(59, 130, 246, 0.5);
}

.format-image {
  width: 100%;
  border-radius: 0.5rem;
  margin-top: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Action Buttons */
.noc-action-buttons {
  display: flex;
  gap: 0.75rem;
  padding: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 600;
  transition: all 0.3s ease;
  flex: 1;
}

.btn-primary {
  background: rgba(59, 130, 246, 0.8);
  color: white;
}

.btn-primary:hover {
  background: rgba(59, 130, 246, 1);
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-primary);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .noc-content {
    grid-template-columns: 1fr;
  }

  .noc-preview-section {
    max-height: 300px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 1rem;
  }
}

@media (max-width: 768px) {
  .noc-creation-panel {
    padding: 0.75rem;
  }

  .noc-header {
    padding: 0.75rem;
    flex-direction: column;
    align-items: flex-start;
  }

  .noc-actions {
    width: 100%;
  }

  .noc-actions .btn {
    flex: 1;
  }

  .signatory-fields {
    grid-template-columns: 1fr;
  }

  .license-info {
    flex-direction: column;
  }

  .license-field {
    width: 100%;
    min-width: unset;
  }
}

@media (max-width: 480px) {
  .noc-creation-panel {
    padding: 0.5rem;
    gap: 0.5rem;
  }

  .noc-header {
    padding: 0.5rem;
  }

  .form-card,
  .preview-card {
    padding: 0.75rem;
  }

  .form-group label,
  .form-card-title {
    font-size: 0.75rem;
  }

  .condition-item,
  .signatory-card {
    padding: 0.5rem;
    gap: 0.5rem;
  }

  .condition-number {
    width: 1.5rem;
    height: 1.5rem;
    min-width: 1.5rem;
  }

  .condition-remove {
    width: 1.5rem;
    height: 1.5rem;
    min-width: 1.5rem;
  }

  .noc-action-buttons {
    flex-direction: column;
    padding: 0.75rem;
  }

  .btn {
    padding: 0.5rem 0.75rem;
    font-size: 0.75rem;
  }
}

/* Scrollbar Styling */
.noc-form-section::-webkit-scrollbar,
.noc-preview-section::-webkit-scrollbar,
.preview-content::-webkit-scrollbar {
  width: 6px;
}

.noc-form-section::-webkit-scrollbar-track,
.noc-preview-section::-webkit-scrollbar-track,
.preview-content::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
}

.noc-form-section::-webkit-scrollbar-thumb,
.noc-preview-section::-webkit-scrollbar-thumb,
.preview-content::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
}

.noc-form-section::-webkit-scrollbar-thumb:hover,
.noc-preview-section::-webkit-scrollbar-thumb:hover,
.preview-content::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.3);
}

/* Text Utilities */
.text-xs {
  font-size: 0.75rem;
}

.text-sm {
  font-size: 0.875rem;
}

.text-lg {
  font-size: 1.125rem;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: 700;
}

.text-primary {
  color: var(--text-primary);
}

.text-secondary {
  color: var(--text-secondary);
}

.text-tertiary {
  color: var(--text-tertiary);
}
